.popup_wrapper {
  width: fit-content;
  height: fit-content;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  border-radius: 16px;
  z-index: 999;
}

.popup_backdrop {
  position: fixed;
  /* From https://css.glass */
  background: rgba(28, 18, 18, 0.47);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  z-index: 999;
  width: 100%;
  height: 100%;
}
