.loginHome {
  width: 350px;
  margin: auto;
  padding: 100px 0;
  .inputLogin {
    border: 1px solid rgb(234, 234, 234);
    border-radius: 5px;
    padding: 40px 30px 10px;
    margin-bottom: 20px;
    & > * {
      margin-bottom: 1rem;
    }
    .logo {
      color: #000000;
      font-weight: 200;
      font-size: 35px;
      line-height: 0.65;
      font-family: "Lobster", cursive;
      text-align: center;
      padding: 30px 0 50px 0;
    }
    .input {
      display: block;
      text-align: center;
      & > * {
        width: 100%;
        margin-bottom: 0.5rem;
        height: 30px;
        background-color: #f5f5f5;
        border: 1px solid #e0e0e0;
        border-radius: 2px;
        padding-left: 10px;
      }
      button {
        margin-top: 0.5rem;
        width: 100%;
        height: 35px;
        font-weight: 600;
        color: white;
        background-color: rgb(121, 181, 244);
        border-radius: 10px;
        margin: 10px 5px;
      }
    }
  }
  .signUp {
    border: 1px solid rgb(234, 234, 234);
    border-radius: 5px;
    padding: 20px 30px;
    display: flex;
    font-size: 15px;
    justify-content: center;
  }
  .login-btn {
    cursor: pointer;
    background-color: rgb(0, 149, 246) !important;
    &:hover {
      background-color: #1877f2 !important;
    }
  }
}

.footer {
  font-size: 15px;
  color: #a1a1a1;
  ul {
    display: flex;
    justify-content: center;
  }
}
